*, *::before, *::after {
  margin: 0;
  bottom: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  display: grid;
  place-items: center;
}

main {
  position: relative;
  overflow: hidden;
  border-radius: 0.3rem;
  width: 12rem;
  transition: all 1s ease;
  animation: interload 1s forwards;
}

article {
  line-height: 1.5;
  font-size: 0.563rem;
  padding: 1.5rem 1.5rem;
}

img {
  width: 2rem;
  margin-bottom: 0.5rem;
}

h2 {
  font-size: 1.2rem;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}

button {
  font-size: 0.6rem;
  width: 5.5rem;
  padding: 0.6rem;
  border: none;
  border-radius: 1rem;
  margin-top: 0.8rem;
}

footer {
  position: absolute;
  bottom: 0;
}

@media (min-width: 800px) {
  main {
    width: 40rem;
    display: grid;
    height: 20rem;
    grid-template-columns: 1fr 1fr 1fr;
  }
  article {
    padding: 2rem;
    font-size: 0.75rem;
  }
  button {
    margin-top: 3rem;
  }
  h2 {
    font-size: 1.5rem;
  }
}
@keyframes interload {
  0% {
    transform: translateY(-7rem);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}